<template>
  <div>
    <el-table
      :data="dataSource"
      style="width: 100%"
    >
      <el-table-column prop="name" label="待评分模板名称" />
      <el-table-column prop="type" label="标准模板名称" />
      <el-table-column prop="time" label="评分时间" />
      <el-table-column prop="code" label="得分" />
      <el-table-column prop="progress" label="评分进度" />
      <el-table-column prop="oprate" label="操作">
        <template #default="{row}">
          <div>
            <el-link style="margin-right: 10px;" type="primary" @click="seeHandle(row)">查看评分详情</el-link>
            <el-link type="primary" @click="loadHandle(row)">导出评分PDF</el-link>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { ref } from 'vue'
import { ElMessage } from 'element-plus'

export default {
  setup() {
    const dataSource = ref([
      { name: '测试一下', type: '测测', time: '2021-01-02', code: 90, progress: '80%' }
    ])
    const seeHandle = () => {
      ElMessage.success('查看')
    }
    const loadHandle = () => {
      ElMessage.success('导出成功')
    }

    return {
      dataSource,
      seeHandle,
      loadHandle
    }
  },
}
</script>
